.pursuit-count {
  height: 430px;
  width: 93%;
  float: left;
  margin: 24% 0 0 6.8%;
  font-size: 26px;
  h2 {
    color: #48f0ff;
    font-size: 60px;
    position: absolute;
    top: 20px;
    width: 100%;
    text-align: center;
    left: 0;
  }
  li {
    float: left;
    margin-bottom: 50px;
    p {
      float: left;
    }
    &:nth-child(2),
    &:nth-child(3) {
       width: 100%;
    }
    &:nth-child(4),
    &:nth-child(6) {
      width: 59%;
      .count-value {
        width: 195px;
      }
    }
    &:nth-child(5),
    &:nth-child(7) {
      width: 39%;
      .count-value {
        width: 130px;
      }
      .count-name {
        background: url(./images/count-bg3.png) no-repeat center / 100% 100%;
        width: 110px;
        height: 53px;
        line-height: 53px;
      }
    }
    
    .count-name {
      background: url(./images/count-bg1.png) no-repeat center / 100% 100%;
      width: 233px;
      height: 55px;
      line-height: 55px;
      text-align: center;
      margin-right: 8px;
    }
    .count-value {
      background: url(./images/count-bg2.png) no-repeat center / 100% 100%;
      width: 487px;
      height: 55px;
      line-height: 55px;
      font-size: 42px;
      color: #2afff7;
      padding-left: 20px;
      font-family: digifacewide;
    }
  }
  
}


