.job-check {
  width: 100%;
  height: 936px;
  float: left;
  h2 {
    background: url("./images/title-bg.png") no-repeat center;
    width: 100%;
    text-align: center;
    color: #48f0ff;
    height: 60px;
    line-height: 60px;
    font-size: 60px;
  }
  .count-total {
    width: 100%;
    float: left;
    margin-top: 35px;
    li {
      width: 245px;
      height: 160px;
      float: left;
      list-style: none;
      margin-left: 40px;
      &:nth-child(1) {
        i {
          margin-top: 63px;
        }
        p {
          font-size: 68px;
          color: #1cffff;
        }
        span {
          margin-bottom: 15px;
        }
      }
      i {
        background: url("./images/icon.png") no-repeat center / 100% 100%;
        display: inline-block;
        width: 34px;
        height: 36px;
        float: left;
        margin: 0 10px 0 20px;
      }
      span {
        float: left;
        font-size: 32px;
        color: #fff;
        margin-bottom: 30px;
      }
      p {
        font-size: 50px;
        color: #ff942c;
        padding-left: 30px;
        i {
          background: url("./images/arrow.png") no-repeat;
          width: 29px;
          height: 42px;
          float: inherit;
        }
      }
    }
  }
  .job-check-charts {
    margin-top: 30px;
    float: left;
  }
}


