.register-case {
  float: left;
  width: 100%;
  height: 640px;
  margin-top: 120px;
  h2 {
    background: url("./images/title-bg.png") no-repeat center;
    width: 100%;
    text-align: center;
    color: #48f0ff;
    height: 60px;
    line-height: 60px;
    font-size: 60px;
  }
  .register-case-charts {
    margin: 60px 0 0 20px;
      /*轴线*/
      .axis path,
      .axis line {
        fill: none;
        stroke: none;
      }

      .axis text {
        font-size: 20px;
        stroke: none;
        fill: #fff;
      }

      /* 网格线 */
      .grid-line path,
      .grid-line line {
        fill: none;
        stroke: #00ffff;
        opacity: 0.5;
      }
      .grid-line-y line{
        stroke-width: 45px;
        stroke: #052d4f;
        stroke-opacity: 0.8;
      }
      .grid-line-y .tick:first-child line{
        stroke-width: 1px;
        stroke: #00ffff;
        stroke-opacity: 1;
        opacity: 1;
      }

      .axis-x path,
      .axis-y path {
        stroke: #00ffff;
        shape-rendering: optimizeSpeed;
      }
    }
}


