
.solve-case {
  margin-top: 30px;
  h2 {
    width: 100%;
    text-align: center;
    color: #48f0ff;
    height: 60px;
    line-height: 60px;
    font-size: 60px;
  }
  .pieCharts {
    float: left;
    margin: 120px 0 0 49px;
  }
  .ranking-list {
    float: left;
    width: 305px;
    margin-top: 30px;
    font-size: 24px;
    color: #d2eaff;
    li {
      float: left;
      margin-bottom: 20px;
      span {
        display: inline-block;
        float: left;
        &:nth-child(2) {
          width: 135px;
        }
        &:nth-child(3) {
          width: 90px;
        }
      }
      i {
        background: url("./images/rank-icon.png") no-repeat;
        width: 75px;
        height: 31px;
        display: inline-block;
        float: left;
        padding-left: 2px;
      }
    }
  }
  .ranking-title {
    margin-top: 130px;
    float: left;
    font-size: 24px;
    color: #48f0ff;
    width: 305px;
    span {
      display: inline-block;
      float: left;
      &:nth-child(1) {
        width: 75px;
      }
      &:nth-child(2) {
        width: 135px;
      }
      &:nth-child(3) {
        width: 90px;
      }
    }
  }
}


